import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { UsersComponent } from './user/users/users.component';
import { RegisterComponent } from './user/register/register.component';
import { ProfileComponent } from './user/profile/profile.component';
import { GoodsComponent } from './goods/goods/goods.component';
import { AddGoodComponent } from './goods/add-good/add-good.component';
import { EditGoodComponent } from './goods/edit-good/edit-good.component';
import { AddCategoryComponent } from './category/add-category/add-category.component';
import { EditCategoryComponent } from './category/edit-category/edit-category.component';
import { ListsComponent } from './category/lists/lists.component';

const routes: Routes = [
  { path: 'admin', component: DashboardComponent },
  { path: 'admin/dashboard', redirectTo: 'admin' },
  { path: 'admin/login', component: LoginComponent },
  { path: 'admin/user/users', component: UsersComponent },
  { path: 'admin/user/register', component: RegisterComponent },
  { path: 'admin/user/profile', component: ProfileComponent },
  { path: 'admin/user', redirectTo: 'admin/user/users'},
  { path: 'admin/goods/register', component: AddGoodComponent},
  { path: 'admin/goods/edit/:id', component: EditGoodComponent},
  { path: 'admin/goods/lists', component: GoodsComponent },
  { path: 'admin/goods', redirectTo: 'admin/goods/lists'},
  { path: 'admin/category/register', component: AddCategoryComponent},
  { path: 'admin/category/edit/:id', component: EditCategoryComponent},
  { path: 'admin/category/lists', component: ListsComponent},
  { path: 'admin/category', redirectTo: 'admin/category/lists'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
